import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function continuousOperationsProfitMarginTtm(tableName: string) {
  const title = "Core Profit Margin (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["continuousOperationsProfitMarginTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Profit from regular business only.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about core profit margin?`,
              value:
                info.row.original.financialData.companyRatios?.data
                  ?.continuousOperationsProfitMarginTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyRatios?.data
              ?.continuousOperationsProfitMarginTtm ?? 0) * 100
          }
        />
      )
    },
  })
}

export { continuousOperationsProfitMarginTtm }
